const logic = (ans,cpu,w) => {
  cpu.flag_assign("CF",0)
  cpu.flag_assign("OF",0)
  let mask = 1 << ((w+1)*8)
  cpu.flag_assign("ZF", !(ans % mask))
  cpu.flag_assign("SF", ans & (1 << ((w+1) * 8 - 1)))
  cpu.flag_assign("PF", !([...Array((w+1)*2).keys()].map((i) => {return !!(ans & (1<<i))}).reduce((a, b) => a + b, 0) % 2))
}
